html {
  scroll-behavior: smooth;
}

.MainContent {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: noto-sans-cjk-kr,sans-serif;
  font-size: 16px;
}

.MainBanner {
  width: 100%;
  height: 640px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f8f8f8;
  padding-bottom: 80px;
}

.MainRecipeRank {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 60px 0;
}

#RecipeTitle, #DietTitle {
  font-size: 24px;
  font-weight: 500;
  padding: 16px 0;
}

#RecipeSubTitle, #DietSubTitle{
  color: #999999;
}

.MainRecipeRankContent, .MainDietRankContent{
  display: flex;
  justify-content: center;
  align-content: center;
  padding: 20px 0;
}

.MainDietRankContent .MainRecipeImg {
  background-color: #ffffff;
}

.MainRecipeRankBox {
  width: 320px;
  height: 320px;
  padding: 30px;
  transition: all 0.2s linear;
  cursor: pointer;
}

.MainRecipeRankBox:hover {
  transform: scale(1.1, 1.1);
}

.MainRecipeImg {
  width: 320px;
  height: 240px;
  background-color: #f8f8f8;
}

.MainRecipeName {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}

.MainRecipeName span:nth-child(1) {
  font-size: 16px;
  padding: 16px 0;
}

.MainRecipeName span:nth-child(2) {
  padding: 16px 8px;
}

.MainRecipeHashtag {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;
}

.MainRecipeHashtag span {
  font-size: 14px;
  width: auto;
  padding: 4px;
  padding-right: 16px;
  color: #999999;
}

.MainNewRecipe {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 60px 0;
  background-color: #f8f8f8;
}

.MainNewRecipeBox {
  width: 1440px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  padding: 60px 0;
}

.MainNewRecipeImg {
  width: 420px;
  height: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ffffff;
}

.MainNewRecipeInfo {
  width: 420px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.MainNewRecipe #recipeDescription {
  color: #999999;
}

.MainNewRecipeName {
  display: flex;
  flex-direction: row;
  padding: 16px 0;
  align-items: center;
}

.MainNewRecipeName span {
  font-size: 20px;
  font-weight: 500;
  padding-right: 8px;
}

.MainRevNewRecipeBox {
  width: 1440px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  padding: 40px 0;
}

.MainAllViewBtn, .MainCreateBtn{
  width: 120px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #999999;
  border-radius: 30px;
  background-color: #ffffff;
  cursor: pointer;
}

.MainRecipeKeyword, .MainDietKeyword{
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 60px 0;
}

.MainKeyword {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* width: 720px; */
  padding: 40px 0;
}

.MainSearchBox {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;  
  padding: 40px 0;
}

.MainSearchBox #searchIcon {
  color: rgb(153, 153, 153);
  position: absolute;
  left: 40px;
} 

.MainSearchBox #inputRecipeKeyword, .MainSearchBox #inputDietKeyword {
  width: 720px;
  padding: 16px 60px;
  border: 1px solid #999999;
  border-radius: 30px;
  outline: none;
  margin-left: 10px;
  font-size: 16px;
}

.MainSearchBox #inputKeyword:focus {
  border: 1px solid rgb(50, 220, 199);
}

.MainSearchBox input:-webkit-autofill {
  box-shadow: 0 0 0 1000px #fff inset;
}

.MainKeywordBox {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.MainKeywordAll {
  width: auto;
  height: 30px;
  border-radius: 30px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-bottom: 10px;
  margin-left: 10px;
  margin-right: 10px;
  border: 1px solid rgb(50, 220, 199);
  cursor: pointer;
}

.MainKeywordAll span {
  color: rgb(50, 220, 199);
  padding: 16px;
}

.MainKeywordValue {
  width: auto;
  height: 30px;
  border-radius: 30px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-bottom: 10px;
  margin-left: 10px;
  margin-right: 10px;
  border: 1px solid #999999;
  cursor: pointer;
}

.MainKeywordValue span {
  padding: 16px;
}

.MainSearchResultBox {
  width: 80%;
  min-height: 480px;
  height: auto;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.MainSearchResultBox #notSearchResult {
  display: none;
}

.MainSearchResult {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.MainDietRank {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #f8f8f8;
  padding: 40px 0;
}

.MainDietCreate {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 40px 0;
}

.MainRegi {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 40px 0;
}