.DietCreateContent {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 80px 0;
  font-family: noto-sans-cjk-kr,sans-serif;
  font-size: 16px;
}

.RecipeSelectBox {
  width: 1440px;
  display: flex;
  /* justify-content: center; */
  justify-content: space-around;
  padding: 80px 0;
}

.Recipe {
  display: flex;
  flex-direction: column;
}

.Recipe #time {
  font-size: 20px;
  font-weight: 500;
  color: rgb(50, 220, 199);
  padding: 16px 0;
}

.Recipe #recipeName .Recipe #recipeComment {
  font-size: 16px;
}

.Recipe span {
  padding: 4px 0;
}

.RecipeName {
  display: flex;
  flex-direction: column;
  padding: 8px 0;
}

.RecipeName span {
  padding: 4px 0;
}

.RecipeComment {
  display: flex;
  flex-direction: column;
  padding: 4px 0;
}

.RecipeComment span {
  padding: 4px 0;
}

.RecipeImgBox {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 320px;
  height: 320px;
  background-color: #f8f8f8;
  cursor: pointer;
}

.DietInfoBox {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 1080px;
  padding: 60px 0;
  /* align-items: flex-start; */
}

.DietInfoBox #dietInfoInput {
  font-size: 20px;
  font-weight: 500;
  color: rgb(50, 220, 199);
  padding: 20px 0;
}

.DietName {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 20px 0;
}

.DietName span {
  font-size: 20px;
  font-weight: 500;
}

.DietName input {
  width: 240px;
  margin: 16px;
  border: none;
  border-bottom: 1px solid #999999;
  font-size: 16px;
}

.DietComment {
  display: flex;
  flex-direction: column;
  padding: 20px 0;
}

.DietComment span {
  font-size: 20px;
  font-weight: 500;
  padding: 16px 0;
}

.DietComment textarea {
  height: 240px;
  font-size: 16px;
}

.DietHashTag {
  display: flex;
  flex-direction: column;
  padding: 20px 0;
}

.DietHashTag span {
  font-size: 20px;
  font-weight: 500;
  padding: 4px 0;
}

.DietHashTag #sub {
  font-size: 14px;
  font-weight: 400;
  color: #999999;
}

.DietHashTag input {
  margin: 16px 0;
  border: none;
  border-bottom: 1px solid #999999;
  font-size: 16px;
}

input:focus {
  outline: none;
  border-bottom: 1px solid rgb(50, 220, 199);
}

input:-webkit-autofill, textarea:-webkit-autofill{
  box-shadow: 0 0 0 1000px #fff inset;
}

textarea {
  resize: none;
  border: 1px solid #999999;
}

textarea:focus {
  outline: none;
  border: 1px solid rgb(50, 220, 199);
}

.RegiBtn {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 40px 0;
  cursor: pointer;
}

.RegiBtn div {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 120px;
  height: 40px;
  border: 1px solid #999999;
  border-radius: 30px;
}